@import url(https://fonts.googleapis.com/css?family=Karla|Nunito|Rock+Salt);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

/* body {
  background-color: #FBFAF9 !important;
} */
body{
  background-color: #FFFFFF !important;
}
h2, h3, h4, h5, h6, p{
  font-family: Nunito !important;
}
h1 {
  font-family: Rock Salt !important;
}
p{
  font-size: 1.2em;
}
.menu{
  justify-content: center;
}
.initialHeader{
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-transition: top 1s ease;
  transition: top 1s ease;
}
.mainHeader{
  position: absolute;
  top: 10em;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-transition: top 1s ease;
  transition: top 1s ease;
}
.container{
  position: absolute;
  top: 16em;
  width: 100%;
}

.centered{
  text-align: center;
}
.accent{
  color: #002642 !important;
}
.accent-button{
  background-color: #002642 !important;
  color: white !important;
}
.accent-weight{
  font-weight: bolder;
}
.aboutList{
  width: 90%;
  margin: 1em auto !important;
}
.galleryButtons{
  background-color: #002642 !important;
  color: white !important;
}
.aboutContent{
  margin: 2em auto 0 auto;
  width: 75%;
}
.projectContent{
  margin: 2em auto;
  width: 40%;
}
@media only screen and (max-width: 767px) {
  .project-overflow{
    overflow: auto;
    max-height: 360px; 
    position: relative;
  }
  .aboutGallery {
    display: none !important;
  }
}
@media only screen and (min-width: 767px) {
  .aboutContent {
    width: 55%;
  }
}
@media only screen and (min-width: 1400px) {
  .aboutContent {
    width: 35%;
  }
  .projectContent {
    width: 35%;
  }
}
figure.projectImage {
  position: relative;
  overflow: hidden;
  margin: 0;
  width: 100%;
  color: #ffffff;
  text-align: center;
  font-size: 16px;
  background-color: #525252;
  border-radius:50%;
  border: 1px solid #525252;
	 box-shadow: 0 9px 6px -6px #525252;
}

figure.projectImage *,
figure.projectImage *:before,
figure.projectImage *:after {
  box-sizing: border-box;
  -webkit-transition: all 0.55s ease;
    transition: all 0.55s ease;
}
figure.projectImage .title {
  position: absolute;
  width: 80%;
  top: 50%;
  left: 50%;
  padding: 5px 10px;
  -webkit-transform: translateY(-50%, -50%);
  transform: translate(-50%, -50%);
}
figure.projectImage .title:before,
figure.projectImage .title:after {
  height: 2px;
  width: 400px;
  position: absolute;
  content: '';
  background-color: #ffffff;
}
figure.projectImage .title:before {
  top: 0;
  left: 10px;
  -webkit-transform: translateX(100%);
  transform: translateX(100%);
}
figure.projectImage .title:after {
  bottom: 0;
  right: 10px;
  -webkit-transform: translateX(-100%);
  transform: translateX(-100%);
}
figure.projectImage .title div:before,
figure.projectImage .title div:after {
  width: 2px;
  height: 300px;
  position: absolute;
  content: '';
  background-color: #ffffff;
}
figure.projectImage .title div:before {
  top: 10px;
  right: 0;
  -webkit-transform: translateY(100%);
  transform: translateY(100%);
}
figure.projectImage .title div:after {
  bottom: 10px;
  left: 0;
  -webkit-transform: translateY(-100%);
  transform: translateY(-100%);
}
figure.projectImage h5,
figure.projectImage h6 {
  margin: 0;
  text-transform: uppercase;
}
figure.projectImage h5 {
  font-size:.9rem;
  font-weight: 500;
  color: #002642;
}
figure.projectImage p {
  font-size: 1.35em;
  font-weight: 400;
}
figure.projectImage h6 {
  font-weight: 700;
  color: #000000;
}
.white-bg{
  background-color: #ffffff;
  padding: 5px 10px;
}
