figure.projectImage .fake-link {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  cursor: pointer;
}
figure.projectImage:hover img,
figure.projectImage.hover img {
  filter: alpha(opacity=35);
  -webkit-opacity: 0.35;
  opacity: 0.25;
}
figure.projectImage:hover .title:before,
figure.projectImage.hover .title:before,
figure.projectImage:hover .title:after,
figure.projectImage.hover .title:after,
figure.projectImage:hover .title div:before,
figure.projectImage.hover .title div:before,
figure.projectImage:hover .title div:after,
figure.projectImage.hover .title div:after {
  -webkit-transform: translate(0, 0);
  transform: translate(0, 0);
}
figure.projectImage:hover .title:before,
figure.projectImage.hover .title:before,
figure.projectImage:hover .title:after,
figure.projectImage.hover .title:after {
  -webkit-transition-delay: 0.15s;
  transition-delay: 0.15s;
}
figure.projectImage:hover figcaption,
figure.projectImage.hover figcaption,
figure.projectImage:hover .white-bg,
figure.projectImage.hover .white-bg{
  opacity: 1;
  -webkit-transition-delay: 0.2s;
  transition-delay: 0.2s;
}
